.ProductPageNumber{
    width: 100%;
    display: flex;
}

.Select-Container{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background-color: rgba(112, 107, 107, 0.35);
    padding: 5px;
    border-radius: 4px;
}

.Select-Container h1{
    font-size: 15px;
    font-weight: 400;
}

.Select-Number{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 40%;
    border-radius: 4px;
    background-color: #D9D9D9;
    font-size: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.Select-Number input{
    background: none;
    border: none;
    text-align: center;
    font-size: 15px;
}

.Select-Number input::-webkit-outer-spin-button,
.Select-Number input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.Select-Number input:focus {
    outline: none;
}  

.ProductPageNumber-button{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    border: none;
}

@media (min-width: 1000px){
    .ProductPageNumber {
        width: 90%;
        height: 35px;
    }

    .Select-Number{
        width: 35%;
    }
}

@media (min-width: 1200px){
    .ProductPageNumber {
        width: 80%;
        height: 40px;
    }

    .Select-Number{
        width: 30%;
    }
}

